.localsContainer {
    display: flex;
    justify-content: center;
    width: 100%;
    justify-content: center;

}


.infoContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 40vh;
    background-color: #fdfbf2c0;
    border-radius: 11px;
}

@media (max-width: 704px) {
    .infoContainer {
        width: 100%;
    }
}

.CardContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 30vw;
    height: 40vh;
}

@media (max-width: 704px) {
    .CardContainer {
        width: 100%;
    }
}

.Card {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-color: #fdfbf2;
    box-shadow: 0px 4px 19px 7px rgb(0 0 0 / 25%);
    border-radius: 11px;
    height: 66vh;
}

@media (max-width: 704px) {
    .Card {
        width: 280px;
        height: 40vh;
    }
}

.CardImage {
    height: 46vh;
    width: 30vw;
    object-fit: cover;
}

@media (max-width: 704px) {
    .CardImage {
        width: 95%;
        height: 20vh;
        /* grid-template-columns: 200px auto; */
    }
}

.arrows {
    font-size: 30px;
    height: 66vh;
    background-color: transparent;
    color: #40047C;
}

@media (max-width: 704px) {
    .arrows {
        height: 40vh;
    }
}

.arrows:hover {
    cursor: pointer;
}

.verDetalles {
    background: #40047C;
    border-radius: 5px;
    padding: 5px;
    width: 50%;
    color: #E7E7E7;
    font-family: 'Cambay', sans-serif;
    font-size: 14px;
}

.verDetalles:hover {
    cursor: pointer;
    background-color: #40047ce4;
    border-radius: 5px;
    color: white;
}

.editarBar {
    background: #40047C;
    border-radius: 5px;
    padding: 5px;
    width: 100%;
    color: #E7E7E7;
    font-family: 'Cambay', sans-serif;
    font-size: 14px;
}

.editarBar:hover {
    cursor: pointer;
    background-color: #40047ce4;
    border-radius: 5px;
    color: white;
}

.volver {
    background: #E7E7E7;
    border-radius: 5px;
    padding: 5px;
    width: 100%;
    color: #40047C;
    font-family: 'Cambay', sans-serif;
    font-size: 14px;
}

.volver:hover {
    cursor: pointer;
    background-color: #E7E7E7;
    border-radius: 5px;
    color: 40047C;
}

.buttonsContainer {
    display: flex;
    /* align-items: center;
    justify-content: center; */
    flex-direction: column;
    margin: 0px;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-right: 0px;
    gap: 20px;
    width: 80px;
    margin-left: 120px;
}
.allInfo{
    display: flex;
    flex-direction: column;
}
